import {Game} from "../Game";
import {Player} from "../Player";
import {ResultType, Round} from "../Round";

const fabi: Player = {
    id: '1',
    name: 'Fabian'
}
const chrissi: Player = {
    id: '2',
    name: 'Chrissi'
}
const joschi: Player = {
    id: '3',
    name: 'Joschi'
}
const marcus: Player = {
    id: '4',
    name: 'Marcus'
}
const marci: Player = {
    id: '5',
    name: 'Marci'
}

const createRound = (id: string, points: number): Round => {
    const  resultsMap= new Map<Player, number>();

    resultsMap.set(fabi, -1);
    resultsMap.set(chrissi, 0);
    resultsMap.set(joschi, 0);
    resultsMap.set(marcus, 0);
    resultsMap.set(marci, -1);

    return {
        id: id,
        roundPoints: points,
        results: resultsMap
    }
}

export const bspGame = {
    players: [fabi, chrissi, marcus, joschi, marci],
    rounds: [
        createRound('1', 2),
        createRound('2', 2),
        createRound('3', 2),
        createRound('4', 2),
        createRound('5', 2),
        createRound('6', 2),
        createRound('7', 2),
        createRound('8', 2),
        createRound('9', 2),
        createRound('10', 2),
        createRound('11', 2),
        createRound('12', 2),
    ],
    result: new Map<Player, number | undefined>()
} as Game;